import React from 'react';
import { Button, Layout, Space } from 'antd';
import { Navigate } from 'react-router-dom';
import styles from './Preview.module.css';
import dayjs from 'dayjs';

const { Header, Content } = Layout;

function isEmpty(obj) {
  return Object.keys(obj).length === 0;
}

class Preview extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      submit: true,
      hide: false,
      template: { title: '', content: [] },
    };
  }

  componentDidMount() {
    this.setState({ template: JSON.parse(sessionStorage.getItem('template')) });
  }

  render() {
    const bill = JSON.parse(sessionStorage.getItem('bill'));

    window.onafterprint = () => {
      this.setState({ hide: false });
    };

    return this.state.submit ? (
      <div>
        {
          <Layout>
            {!this.state.hide && (
              <Header
                style={{ position: 'sticky', top: 0, zIndex: 1, width: '100%' }}
              >
                <Space className={styles.method}>
                  <Button
                    onClick={() => {
                      this.setState({ submit: false });
                    }}
                  >
                    
                  </Button>
                  <Button
                    onClick={() => {
                      this.setState({ hide: true });
                      setTimeout(() => {
                        window.print();
                      }, 0);
                    }}
                  >
                    
                  </Button>
                </Space>
              </Header>
            )}

            <Layout>
              <Content
                style={{
                  overflow: 'initial',
                  background: 'white',
                  padding: '45px',
                }}
              >
                <h2>{this.state.template.title}</h2>
                {this.state.template.content
                  .map((item) => {
                    return this.renderItem(item, bill, item.name);
                  })
                  .flat()}
              </Content>
            </Layout>
          </Layout>
        }
      </div>
    ) : (
      <Navigate replace to='/'></Navigate>
    );
  }
  renderItem(item, data, name, level = 1) {
    const { type, title, children = [] } = item;

    let ele;
    switch (type) {
      case 'GroupTitle':
        if (data[name] !== undefined && !isEmpty(data[name])) {
          ele = [<h3 style={{ marginBottom: '10px' }}>{title}</h3>];
          children.forEach((child) => {
            ele.push(
              this.renderItem(child, data[name], child.name[level], level + 1)
            );
          });
        }
        break;
      case 'Group':
        if (data[name] !== undefined && !isEmpty(data[name])) {
          ele = [];
          ele.push(
            <div style={{ marginLeft: '25px', marginBottom: '10px' }}>
              <h4 style={{ margin: '0px 0px 5px 0px' }}>{title}</h4>
              {children.map((child) =>
                this.renderItem(child, data[name], child.name[level], level + 1)
              )}
            </div>
          );
        }
        break;
      case 'Divider':
        break;
      case 'Date':
        if (data[name] !== undefined) {
          ele = (
            <div style={{ marginLeft: '25px', marginBottom: '10px' }}>{`${
              title ?? name
            }: ${dayjs(data[name]).format('MM/DD/YYYY')}`}</div>
          );
        }
        break;
      default:
        if (data[name] !== undefined) {
          ele = (
            <div style={{ marginLeft: '25px', marginBottom: '10px' }}>{`${
              title ?? name
            }: ${data[name]}`}</div>
          );
        }
        break;
    }

    return ele;
  }
}

export default Preview;
