import React from 'react';
import {
  Button,
  Form,
  Input,
  DatePicker,
  Select,
  Divider,
  Upload,
  InputNumber,
  Layout,
  Menu,
  Space,
} from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import { Navigate } from 'react-router-dom';
import dayjs from 'dayjs';

import { ArrowRightOutlined } from '@ant-design/icons';

import styles from './MainTemplate.module.css';

const { Header, Content, Sider } = Layout;

const validateDate = function (curr) {
  Object.entries(curr).forEach(([k, v]) => {
    if (Array.isArray(curr)) {
      curr.forEach((obj) => {
        validateDate(obj);
      });
    } else if (typeof v === 'object') {
      validateDate(v);
    } else {
      if (isNaN(+v) && dayjs(v).isValid()) {
        curr[k] = dayjs(v);
      }
    }
  });
};

class MainTemplate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      submit: false,
      template: JSON.parse(sessionStorage.getItem('template')),
    };
  }

  render() {
    const readFile = (file) => {
      const fileReader = new FileReader();
      fileReader.readAsText(file, 'UTF-8');
      fileReader.onload = (e) => {
        const template = JSON.parse(e.target.result);
        this.setState({ template });
        sessionStorage.setItem('template', e.target.result);
      };
      return false;
    };

    const onFinish = (values) => {
      sessionStorage.setItem('bill', JSON.stringify(values));
      this.setState({ submit: true });
    };

    const initialValues = JSON.parse(sessionStorage.getItem('bill')) || {};
    validateDate(initialValues);

    const upload = (
      <Upload accept='.json' showUploadList={false} beforeUpload={readFile}>
        <Button>
          <UploadOutlined /> 
        </Button>
      </Upload>
    );

    const preview = this.state.template && (
      <Button htmlType='submit'></Button>
    );

    const section = this.state.template?.content
      .filter((item) => item.type === 'GroupTitle')
      .map((item) => {
        const anchor = (
          <a
            onClick={() => {
              document.getElementById(item.name).scrollIntoView();
              window.scrollBy(0, -120);
            }}
          >
            {item.title}
          </a>
        );
        return {
          key: item.name,
          icon: <ArrowRightOutlined />,
          label: anchor,
        };
      });

    return this.state.submit ? (
      <Navigate replace to='/preview' />
    ) : (
      <Layout>
        <Header
          style={{ position: 'sticky', top: 0, zIndex: 1, width: '100%' }}
        ></Header>
        <Layout>
          <Sider
            width={200}
            style={{
              overflow: 'auto',
              height: '100vh',
              position: 'fixed',
              left: 0,
              top: '65px',
              bottom: 0,
            }}
          >
            <Menu
              mode='inline'
              defaultSelectedKeys={['1']}
              defaultOpenKeys={['sub1']}
              style={{
                height: '100%',
                borderRight: 0,
              }}
              items={section}
            />
          </Sider>
          <Layout
            style={{
              padding: '0 24px 24px',
            }}
          >
            <Content
              style={{
                padding: 24,
                margin: '24px 0px 0px 200px',
                overflow: 'initial',
                minHeight: 280,
                background: 'white',
              }}
            >
              <>
                <h1 style={{ textAlign: 'center', fontSize: '30px' }}>
                  {this.state.template?.title}
                </h1>
                <Form
                  name='basic'
                  labelCol={{
                    span: 8,
                  }}
                  wrapperCol={{
                    span: 14,
                  }}
                  onFinish={onFinish}
                  autoComplete='off'
                  initialValues={initialValues}
                >
                  <Space className={styles.method}>
                    {upload}
                    {preview}
                  </Space>
                  {this.state.template?.content.map((item) =>
                    this.renderItem(item, false)
                  )}
                </Form>
              </>
            </Content>
          </Layout>
        </Layout>
      </Layout>
    );
  }

  renderItem(item, addon = false) {
    const {
      title = '',
      name = '',
      defaultValue = '',
      rules = [],
      placeholder = '',
    } = item;
    const options =
      item.options?.map((opt) => ({ label: opt, value: opt })) || [];
    let ele = null;
    switch (item.type) {
      case 'Date':
        ele = <DatePicker showToday />;
        break;
      case 'Input':
        ele = (
          <Input
            placeholder={placeholder}
            addonBefore={
              item.addonBefore && this.renderItem(item.addonBefore, true)
            }
            addonAfter={
              item.addonAfter && this.renderItem(item.addonAfter, true)
            }
          ></Input>
        );
        break;
      case 'Select':
        ele = (
          <Select placeholder='Please select' options={options} allowClear />
        );
        break;
      case 'Multiselect':
        ele = (
          <Select
            mode='multiple'
            allowClear
            style={{
              width: '250px',
            }}
            placeholder='Please select'
            options={options}
          />
        );
        break;
      case 'InputNumber':
        ele = <InputNumber />;
        break;
      case 'Divider':
        return (
          <Form.Item wrapperCol={{ offset: 5, span: 14 }} name='divider'>
            <Divider>{title}</Divider>
          </Form.Item>
        );
      case 'TextArea':
        ele = <Input.TextArea defaultValue={defaultValue} autoSize />;
        break;
      case 'Group':
        ele = (
          <Input.Group compact>
            {item.children.map((child) => this.renderItem(child))}
          </Input.Group>
        );
        break;
      case 'GroupTitle':
        return (
          <>
            <Form.Item name={name} wrapperCol={{ offset: 6, span: 12 }}>
              <div className={styles.title}>{item.title}</div>
              <Input.Group>
                <span id={item.name}></span>
                {item.children.map((child) => this.renderItem(child))}
              </Input.Group>
            </Form.Item>
          </>
        );
      default:
        break;
    }

    return (
      <Form.Item
        label={title}
        name={name}
        rules={rules}
        noStyle={addon}
        labelAlign='left'
      >
        {ele}
      </Form.Item>
    );
  }
}

export default MainTemplate;
